    height: 100vh;
}

.bgImg {
    width: 100%;
    height: 100%;

}

.top {
    // width: 100vh;
    // height: 100vh;
    width: 100%;
    height: 100%;
    background-color: rgba(253, 253, 253, 0);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
}

.fan {
    margin: 0.5rem;
    // font-size: 30px;

}

