}

span {
    color: #999;
    font-size: 12px;
}

input::-webkit-input-placeholder {
    color: rgb(228, 228, 228);
}

.loginp {
    color: rgb(238, 238, 238) !important;
    height: 0.8rem;
    width: 90%;
    margin: 0.2rem 0;
    background-color: rgba(255, 255, 255, 0.2);
    border: none;
    text-align: center;

}

.log {
    position: relative;
}

.clear {
    position: absolute;
    right: 1rem;
    top: 0.4rem;
    width: 0.4rem;
    height: 0.4rem;
    border-radius: 0.2rem;
    background-color: rgb(90, 90, 90);
    text-align: center;
    line-height: 0.4rem;
    color: #999;
}

.logbtn {
    color: rgb(71, 71, 71);
